function offset(el) {
  const rect = el.getBoundingClientRect(),
    scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
    scrollTop = window.pageYOffset || document.documentElement.scrollTop;

  return { top: rect.top + scrollTop, left: rect.left + scrollLeft }
}
const animItems = document.querySelectorAll('._anim-items')
function animOnScroll() {
  for (let index = 0; index < animItems.length; index++) {
    const animItem = animItems[index]
    const animItemHeight = animItem.offsetHeight
    const animItemOffset = offset(animItem).top
    const animStart = 4

    let animatemPoint = $win.height() - animItemHeight / animStart
    if (animItemHeight > $win.height()) {
      animatemPoint = $win.height() - $win.height() / animStart
    }

    if (($win.scrollTop() > animItemOffset - animatemPoint) && $win.scrollTop() < (animItemOffset + animItemHeight)) {
      animItem.classList.add('_active')
    } else {

      if (!animItem.classList.contains('_anim-not')) {
        animItem.classList.remove('_active')
      }
    }
  }
}

const $win = $(window);




var elem = document.querySelector("#simple-bar");
var scrollbar = Scrollbar.init(elem,
  {
    speed: 0.1,
    alwaysShowTracks: true,
    // damping:1
  });

if (animItems.length > 0) {
  setTimeout(() => {
    animOnScroll()
  }, 300)
}

const menuBtn = $('.menu__btn');
let scrollPrev = 0;
scrollbar.addListener((s) => {
  let scrolled = s.offset.y; // returns “scrollTop” equivalent
  if ( scrolled > 100 && scrolled > scrollPrev ) {
    menuBtn.removeClass('_active');
  } else {
    menuBtn.addClass('_active');
  }
  scrollPrev = scrolled;
  if (animItems.length > 0) {
    animOnScroll();
  }
})



